import React, { useState } from 'react';
import { ActivityIndicator, FlatList, Text, View, Image, Button } from 'react-native';
import data from './assets/cards.json'
import { Audio } from 'expo-av';

const FlatListItemSeparator = () => {
  return (
    <View
      style={{
        height: 1,
        width: "100%",
        opacity: 0.25,
        backgroundColor: "#000000",
        marginRight: 8
      }}
    />
  );
}

const SoundButton = (props) => {
  async function playSound(cardID, sound) {
    const soundObject = await Audio.Sound.createAsync(
      { uri: 'https://hearthstonesounds.s3.amazonaws.com/'+cardID+'_'+sound+'.wav' },
      { shouldPlay: true }
    );
    try {
      await soundObject.playAsync();
      // Your sound is playing!
      } catch (error) {
      // An error occurred!
      }
  }

  return (
    <Button style={{margin: 4}} title={props.title} onPress= {() => {
      playSound(props.cardID, props.sound)
    }}
    />
  )
}

